<template>
<div style="padding: 3rem; margin: auto; max-width: 600px;">
    <div class="is--center">Login Page</div>
    <div class="large-blur">
        <div style="padding: 5rem;">
          Press Login button if you have saved a token in header of requests
        </div>
        <a
            class="button is--large is--template-border w-inline-block"
            @click="onLogin"
        >
            <div class="copy_code-w">
                <div>Login</div>
            </div>
        </a>
        <div class="is--center" style="padding: 1rem;">
            <a
                class="w-inline-block"
                :href="googleUrl"
            >
                <div class="copy_code-w">
                    <div>Login with Google</div>
                </div>
            </a>
        </div>
    </div>
    <div class="wallpaper"></div>
  </div>
</template>

<script>
import authHeader from '@/middleware/authHeader'

export default {
  name: 'Login',

  data () {
    return {
        user: '',
        password: '',
      // googleUrl: process.env.HOST_NAME + '/auth/google/'
      googleUrl: 'http://api.uflo.io:3000' + '/auth/google/'
    }
  },
  methods: {
      onLogin () {
        const token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VySUQiOiIxMDA2Njk1OTI0ODkyMjQ5Mzc0NDIiLCJlbWFpbCI6ImFzZW1oc0BnbWFpbC5jb20iLCJmaXJzdG5hbWUiOiJBc2VtIiwibGFzdG5hbWUiOiJTYWJlcmkiLCJyb2xlIjoic3R1ZGVudCIsImlhdCI6MTYyMzg1ODIzMCwiZXhwIjoxNjI0NDYzMDMwfQ.-Uf-gC96CgRvJZDi8JzJKEYePMjNSsLIkVm9zk9hfgc'
        this.$store.dispatch('auth/setToken', token)

        this.$axios.get('/users/me', authHeader())
        .then(response => {
          if (response.data.data.role && response.data.data.role !== '') {
            this.$store.dispatch('auth/setUser', response.data.data)
            this.$router.push('/states')
          }
        })
        .catch(() => {})
    }
  },
}
</script>
